.freeboard {
  width: 80%;
  margin: 0 auto;
}

.head-contents {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.head-contents h2 {
  margin-left: 50px;
  color: blue;
  display: inline-block;
  font-size: 30px;
}

.head-contents .input {
  display: flex;
}

.head-contents .input input {
  display: inline-block;
  margin: 0 10px;
  font-size: 15px;
  height: 35px;
}

.head-contents .input button {
  display: inline-block;
  margin: 0px;
  width: 80px;
  font-size: 15px;
  color: blue;
  margin-right: 10px;
}

.freeboard-contents {
  display: block;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  margin-top: 20px;
}

.freeboard-contents a {
  text-decoration: none;
}

/* card */
.FreeCard-div {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  border-bottom: #ddd solid 1px;
  height: 10rem;
}

.FreeCard-title {
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;

  margin-bottom: 15px;
  font-size: 24px;
  text-decoration: none;
  color: #333;
  width: 69vw;
}

.FreeCard-content {
  display: block;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: keep-all;
  white-space: nowrap;
  font-size: 16px;
  color: #333;
  margin-bottom: 20px;
  text-decoration: none;
  width: 69vw;
}

.FreeCard-image {
  height: 8rem;
  width: 8rem;
  object-fit: cover;
}

/*페이지네이션*/
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 30px;
}

.pagination button {
  padding: 10px 20px;
  margin: 0 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: white;
  cursor: pointer;
}

.pagination button.active {
  background-color: #007bff;
  color: white;
  border-color: #007bff;
}

.pagination button:hover {
  background-color: #0056b3;
  color: white;
}
